
* {
    margin: 0;
    border: 0;
    padding: 0;
}
ul, ol, li {
    list-style: none;
}
a {
    color: #657180;
    text-decoration: none;
}
html, body, .examples {
    width: 100%;
    height: 100%;
    color: #657180;
}
body {
    background: #eee;
}
.examples-header {
    padding: 20px 40px;
}
.examples-header h2 {
    display: inline-block;
}
.examples-header > a {
    color: #39f;
    display: inline-block;
    float: right;
}
.examples-nav {
    position: fixed;
    left: 40px;
    top: 100px;
}
.examples-nav .examples-nav-item {
    display: block;
    width: 100%;
    padding: 12px 16px;
    font-size: 16px;
}
.examples-nav .examples-nav-item:hover > a {
    color: #39f;
}
.examples-content {
    width: 90%;
    max-width: 1100px;
    min-width: 900px;
    margin: 30px auto 20px;
    padding: 10px;
}



